@import "../../assets/styles/config";

.CartIcon {
  height: 45px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  .ShoppingIcon {
    width: 24px;
    height: 24px;
  }

  .ItemCount {
    position: absolute;
    font-size: 10px;
    font-weight: bold;
    bottom: 12px;
    color: #fff;
  }
}
